<template>
  <div class="zhihu-hot-section">
    <!-- Ê†áÈ¢òÂå∫Âüü -->
    <div class="section-header">
      <h2>üî• Áü•‰πéÁÉ≠Ê¶úÁõëÊéß</h2>
      <p class="subtitle">GitHub Actions Ëá™Âä®ÊäìÂèñ + GitHub API Â≠òÂÇ®</p>
    </div>

    <!-- GitHub ÈÖçÁΩÆ -->
    <div class="config-panel">
      <div class="config-group">
        <label>GitHub Áî®Êà∑Âêç</label>
        <input
          v-model="config.username"
          type="text"
          placeholder="your-username"
          :disabled="isConfigured"
        />
      </div>
      <div class="config-group">
        <label>‰ªìÂ∫ìÂêç</label>
        <input
          v-model="config.repo"
          type="text"
          placeholder="first_page"
          :disabled="isConfigured"
        />
      </div>
      <div class="config-group">
        <label>Êï∞ÊçÆÊñá‰ª∂Ë∑ØÂæÑ</label>
        <input
          v-model="config.dataPath"
          type="text"
          placeholder="data/zhihu-hot.json"
          :disabled="isConfigured"
        />
      </div>
      <div class="config-actions">
        <button
          v-if="!isConfigured"
          @click="saveConfig"
          class="btn btn-primary"
          :disabled="!canSaveConfig"
        >
          ‰øùÂ≠òÈÖçÁΩÆ
        </button>
        <button
          v-else
          @click="resetConfig"
          class="btn btn-secondary"
        >
          ÈáçÁΩÆÈÖçÁΩÆ
        </button>
        <button
          v-if="isConfigured"
          @click="testConnection"
          class="btn btn-info"
          :disabled="isTesting"
        >
          {{ isTesting ? 'ÊµãËØï‰∏≠...' : 'ÊµãËØïËøûÊé•' }}
        </button>
      </div>
      <div class="config-hint">
        üí° ÊèêÁ§∫ÔºöGitHub Actions ‰ºöÊØè30ÂàÜÈíüËá™Âä®ÊäìÂèñÂπ∂‰øùÂ≠òÊï∞ÊçÆ
      </div>
    </div>

    <!-- ÊéßÂà∂Èù¢Êùø -->
    <div class="control-panel" v-if="isConfigured">
      <div class="control-group">
        <button
          @click="loadData"
          class="btn btn-primary"
          :disabled="isLoading"
        >
          {{ isLoading ? 'Âä†ËΩΩ‰∏≠...' : '‰ªé GitHub Âä†ËΩΩ' }}
        </button>
        <button
          @click="loadLatest"
          class="btn btn-success"
          :disabled="isLoading"
        >
          Âä†ËΩΩÊúÄÊñ∞
        </button>
        <button
          @click="showStats"
          class="btn btn-info"
          :disabled="isLoading"
        >
          ÁªüËÆ°‰ø°ÊÅØ
        </button>
        <button
          @click="triggerManualFetch"
          class="btn btn-warning"
          :disabled="isFetching"
        >
          {{ isFetching ? 'Ëß¶Âèë‰∏≠...' : 'ÊâãÂä®Ëß¶ÂèëÊäìÂèñ' }}
        </button>
      </div>

      <div class="cache-info">
        <span>Êú¨Âú∞ÁºìÂ≠ò: {{ cacheStatus }}</span>
        <button @click="clearCache" class="btn btn-sm">Ê∏ÖÈô§ÁºìÂ≠ò</button>
      </div>
    </div>

    <!-- Áä∂ÊÄÅÊèêÁ§∫ -->
    <div class="status-bar" v-if="statusMessage">
      <div :class="['status', statusType]">
        {{ statusMessage }}
      </div>
    </div>

    <!-- ÈîôËØØÊèêÁ§∫ -->
    <div class="error-bar" v-if="error">
      <div class="error">
        ‚ùå {{ error }}
        <button @click="error = ''" class="btn-close">‚úï</button>
      </div>
    </div>

    <!-- ÂΩìÂâçÁÉ≠Ê¶úÊï∞ÊçÆ -->
    <div class="hot-list-section" v-if="currentHot.length > 0">
      <div class="section-title">
        <h3>üìä ÂΩìÂâçÁÉ≠Ê¶ú ({{ currentHot.length }}Êù°)</h3>
        <span class="timestamp">ÊúÄÂêéÊõ¥Êñ∞: {{ lastUpdateTime }}</span>
      </div>

      <div class="hot-list">
        <div
          v-for="(item, index) in currentHot"
          :key="item.id"
          class="hot-item"
          :class="{ 'top-3': index < 3 }"
        >
          <div class="rank">{{ index + 1 }}</div>
          <div class="content">
            <div class="title">
              <a :href="item.url" target="_blank">{{ item.title }}</a>
            </div>
            <div class="description" v-if="item.description">
              {{ item.description }}
            </div>
            <div class="meta">
              <span class="heat">üî• {{ formatNumber(item.heat) }}</span>
              <span class="time">{{ item.fetchTime }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- ÂéÜÂè≤Âø´ÁÖß -->
    <div class="snapshots-section" v-if="snapshots.length > 0">
      <div class="section-title">
        <h3>üìö ÂéÜÂè≤Âø´ÁÖß ({{ snapshots.length }}Êù°)</h3>
        <button @click="clearHistory" class="btn btn-danger btn-sm">Ê∏ÖÁ©∫Êú¨Âú∞ÂéÜÂè≤</button>
      </div>

      <div class="snapshots-list">
        <div
          v-for="snapshot in snapshots"
          :key="snapshot.id"
          class="snapshot-item"
        >
          <div class="snapshot-header" @click="toggleSnapshot(snapshot.id)">
            <span class="snapshot-time">üìÖ {{ snapshot.formattedTime }}</span>
            <span class="snapshot-count">{{ snapshot.count }} Êù°</span>
            <span class="toggle-icon">{{ expandedSnapshots.has(snapshot.id) ? '‚ñº' : '‚ñ∂' }}</span>
          </div>

          <div
            v-if="expandedSnapshots.has(snapshot.id)"
            class="snapshot-content"
          >
            <div
              v-for="(item, idx) in snapshot.items"
              :key="item.id"
              class="snapshot-item-detail"
            >
              <span class="idx">{{ idx + 1 }}</span>
              <span class="title">{{ item.title }}</span>
              <span class="heat">{{ formatNumber(item.heat) }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Á©∫Áä∂ÊÄÅ -->
    <div class="empty-state" v-if="!isLoading && currentHot.length === 0 && !error">
      <div class="empty-icon">üîç</div>
      <div class="empty-text">ÊöÇÊó†Êï∞ÊçÆÔºåËØ∑ÈÖçÁΩÆ GitHub ‰ø°ÊÅØÂêéÁÇπÂáª"‰ªé GitHub Âä†ËΩΩ"</div>
      <div class="empty-hint">
        üí° ÊèêÁ§∫Ôºö<br/>
        1. ÈÖçÁΩÆ GitHub ‰ªìÂ∫ì‰ø°ÊÅØ<br/>
        2. ÁÇπÂáª"ÊµãËØïËøûÊé•"È™åËØÅ<br/>
        3. ÁÇπÂáª"‰ªé GitHub Âä†ËΩΩ"Êü•ÁúãÊï∞ÊçÆ<br/>
        4. GitHub Actions ‰ºöËá™Âä®ÂÆöÊó∂ÊäìÂèñ
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, computed, onMounted } from 'vue'
import type { ZhihuHotItem, HotSnapshot } from '@/types/zhihu'

// ÈÖçÁΩÆÊé•Âè£
interface GitHubConfig {
  username: string
  repo: string
  dataPath: string
}

// ÈÖçÁΩÆÁä∂ÊÄÅ
const config = ref<GitHubConfig>({
  username: 'geeker-wang',
  repo: 'first_page',
  dataPath: 'data/zhihu-hot.json'
})

// Â∫îÁî®Áä∂ÊÄÅ
const currentHot = ref<ZhihuHotItem[]>([])
const snapshots = ref<HotSnapshot[]>([])
const isLoading = ref(false)
const isTesting = ref(false)
const isFetching = ref(false)
const lastFetchTime = ref<number | undefined>()
const error = ref('')
const statusMessage = ref('')
const statusType = ref<'info' | 'success' | 'warning'>('info')
const expandedSnapshots = ref<Set<string>>(new Set())

// ÁºìÂ≠òÁä∂ÊÄÅ
const cacheStatus = computed(() => {
  const cached = localStorage.getItem('zhihu_cache_timestamp')
  if (!cached) return 'Êó†'
  const time = new Date(parseInt(cached))
  return time.toLocaleString('zh-CN')
})

// ËÆ°ÁÆóÂ±ûÊÄß
const isConfigured = computed(() => {
  return !!config.value.username && !!config.value.repo
})

const canSaveConfig = computed(() => {
  return config.value.username && config.value.repo
})

const lastUpdateTime = computed(() => {
  if (!lastFetchTime.value) return 'ÊöÇÊó†'
  return new Date(lastFetchTime.value).toLocaleString('zh-CN')
})

// ÊñπÊ≥ï
const showStatus = (message: string, type: 'info' | 'success' | 'warning' = 'info', duration: number = 3000) => {
  statusMessage.value = message
  statusType.value = type
  if (duration > 0) {
    setTimeout(() => {
      statusMessage.value = ''
    }, duration)
  }
}

const showError = (message: string) => {
  error.value = message
}

const formatNumber = (num: number): string => {
  if (num >= 1000000) {
    return (num / 1000000).toFixed(1) + 'M'
  } else if (num >= 1000) {
    return (num / 1000).toFixed(1) + 'K'
  }
  return num.toString()
}

// ÈÖçÁΩÆÁÆ°ÁêÜ
const saveConfig = () => {
  try {
    localStorage.setItem('zhihu_github_config', JSON.stringify(config.value))
    showStatus('‚úÖ ÈÖçÁΩÆÂ∑≤‰øùÂ≠ò', 'success')
    // Ëá™Âä®ÊµãËØïËøûÊé•
    testConnection()
  } catch (e) {
    showError(`‰øùÂ≠òÈÖçÁΩÆÂ§±Ë¥•: ${e instanceof Error ? e.message : 'Êú™Áü•ÈîôËØØ'}`)
  }
}

const resetConfig = () => {
  if (confirm('Á°ÆÂÆöË¶ÅÈáçÁΩÆÈÖçÁΩÆÂêóÔºü')) {
    localStorage.removeItem('zhihu_github_config')
    config.value = {
      username: '',
      repo: 'first_page',
      dataPath: 'data/zhihu-hot.json'
    }
    currentHot.value = []
    snapshots.value = []
    showStatus('‚ö†Ô∏è ÈÖçÁΩÆÂ∑≤ÈáçÁΩÆ', 'warning')
  }
}

const loadConfig = () => {
  const saved = localStorage.getItem('zhihu_github_config')
  if (saved) {
    try {
      const parsed = JSON.parse(saved)
      config.value = { ...config.value, ...parsed }
      showStatus('‚úÖ ÈÖçÁΩÆÂ∑≤ÊÅ¢Â§ç', 'info', 2000)
    } catch (e) {
      console.error('Âä†ËΩΩÈÖçÁΩÆÂ§±Ë¥•:', e)
    }
  }
}

// GitHub API Ë∞ÉÁî®
const getGitHubApiUrl = () => {
  return `https://api.github.com/repos/${config.value.username}/${config.value.repo}/contents/${config.value.dataPath}`
}

const fetchFromGitHub = async (): Promise<HotSnapshot[]> => {
  const url = getGitHubApiUrl()
  const response = await fetch(url)

  if (!response.ok) {
    if (response.status === 404) {
      throw new Error('Êï∞ÊçÆÊñá‰ª∂‰∏çÂ≠òÂú®ÔºåËØ∑ÂÖàËøêË°å GitHub Actions ÊäìÂèñÊï∞ÊçÆ')
    }
    if (response.status === 403) {
      throw new Error('API ÈôêÂà∂ÔºåËØ∑Á®çÂêéÂÜçËØïÔºàÊàñ‰ΩøÁî®‰∏™‰∫∫ TokenÔºâ')
    }
    throw new Error(`HTTP ${response.status}: ${response.statusText}`)
  }

  const data = await response.json()

  // Ê≠£Á°ÆËß£Á†Å Base64 + UTF-8
  const binaryString = atob(data.content)
  const bytes = new Uint8Array(binaryString.length)
  for (let i = 0; i < binaryString.length; i++) {
    bytes[i] = binaryString.charCodeAt(i)
  }
  const decoder = new TextDecoder('utf-8')
  const content = decoder.decode(bytes)

  return JSON.parse(content)
}

// ÊµãËØïËøûÊé•
const testConnection = async () => {
  if (!config.value.username || !config.value.repo) {
    showError('ËØ∑ÂÖàËæìÂÖ• GitHub Áî®Êà∑ÂêçÂíå‰ªìÂ∫ìÂêç')
    return
  }

  isTesting.value = true
  error.value = ''

  try {
    const url = getGitHubApiUrl()
    const response = await fetch(url)

    if (response.status === 200) {
      showStatus('‚úÖ GitHub ËøûÊé•ÊàêÂäü', 'success')
    } else if (response.status === 404) {
      showError('Êï∞ÊçÆÊñá‰ª∂‰∏çÂ≠òÂú®Ôºàdata/zhihu-hot.jsonÔºâÔºåËØ∑ÂÖàËøêË°å GitHub Actions')
    } else {
      showError(`ËøûÊé•Â§±Ë¥•: HTTP ${response.status}`)
    }
  } catch (e) {
    showError(`ÊµãËØïÂ§±Ë¥•: ${e instanceof Error ? e.message : 'Êú™Áü•ÈîôËØØ'}`)
  } finally {
    isTesting.value = false
  }
}

// ‰ªé GitHub Âä†ËΩΩÊï∞ÊçÆ
const loadData = async () => {
  if (!isConfigured.value) {
    showError('ËØ∑ÂÖàÈÖçÁΩÆ GitHub ‰ø°ÊÅØ')
    return
  }

  isLoading.value = true
  error.value = ''
  showStatus('üîÑ Ê≠£Âú®‰ªé GitHub Âä†ËΩΩ...', 'info', 0)

  try {
    // Ê£ÄÊü•ÁºìÂ≠òÔºà5ÂàÜÈíüÂÜÖÊúâÊïàÔºâ
    const cacheKey = 'zhihu_cache_data'
    const cacheTimeKey = 'zhihu_cache_timestamp'
    const cached = localStorage.getItem(cacheKey)
    const cacheTime = localStorage.getItem(cacheTimeKey)

    if (cached && cacheTime) {
      const age = Date.now() - parseInt(cacheTime)
      if (age < 5 * 60 * 1000) { // 5ÂàÜÈíü
        console.log('‰ΩøÁî®ÁºìÂ≠òÊï∞ÊçÆ')
        const data = JSON.parse(cached)
        processLoadedData(data)
        showStatus('‚úÖ ‰ªéÁºìÂ≠òÂä†ËΩΩÊàêÂäü', 'success')
        isLoading.value = false
        return
      }
    }

    // ‰ªé GitHub Ëé∑Âèñ
    const data = await fetchFromGitHub()

    if (data.length > 0) {
      // ‰øùÂ≠òÂà∞ÁºìÂ≠ò
      localStorage.setItem(cacheKey, JSON.stringify(data))
      localStorage.setItem(cacheTimeKey, Date.now().toString())

      processLoadedData(data)
      showStatus(`‚úÖ Âä†ËΩΩÊàêÂäü: ${data.length} Êù°ÂéÜÂè≤ËÆ∞ÂΩï`, 'success')
    } else {
      showStatus('‚ÑπÔ∏è ÊöÇÊó†Êï∞ÊçÆ', 'info')
    }
  } catch (e) {
    showError(`Âä†ËΩΩÂ§±Ë¥•: ${e instanceof Error ? e.message : 'Êú™Áü•ÈîôËØØ'}`)
  } finally {
    isLoading.value = false
  }
}

// Â§ÑÁêÜÂä†ËΩΩÁöÑÊï∞ÊçÆ
const processLoadedData = (data: HotSnapshot[]) => {
  snapshots.value = data

  // ÊòæÁ§∫ÊúÄÊñ∞ÁöÑÂø´ÁÖß‰Ωú‰∏∫ÂΩìÂâçÊï∞ÊçÆ
  if (data[0]?.items) {
    currentHot.value = data[0].items
    lastFetchTime.value = data[0].timestamp
  }
}

// Âä†ËΩΩÊúÄÊñ∞
const loadLatest = async () => {
  if (!isConfigured.value) {
    showError('ËØ∑ÂÖàÈÖçÁΩÆ GitHub ‰ø°ÊÅØ')
    return
  }

  isLoading.value = true
  error.value = ''
  showStatus('üîÑ Ê≠£Âú®Âä†ËΩΩÊúÄÊñ∞Êï∞ÊçÆ...', 'info', 0)

  try {
    const data = await fetchFromGitHub()

    if (data.length > 0) {
      currentHot.value = data[0].items
      lastFetchTime.value = data[0].timestamp
      showStatus(`‚úÖ ÊúÄÊñ∞Êï∞ÊçÆ: ${data[0].formattedTime}`, 'success')
    } else {
      showStatus('‚ÑπÔ∏è ÊöÇÊó†Êï∞ÊçÆ', 'info')
    }
  } catch (e) {
    showError(`Âä†ËΩΩÂ§±Ë¥•: ${e instanceof Error ? e.message : 'Êú™Áü•ÈîôËØØ'}`)
  } finally {
    isLoading.value = false
  }
}

// ÊòæÁ§∫ÁªüËÆ°
const showStats = async () => {
  if (!isConfigured.value) {
    showError('ËØ∑ÂÖàÈÖçÁΩÆ GitHub ‰ø°ÊÅØ')
    return
  }

  try {
    const data = await fetchFromGitHub()

    if (data.length === 0) {
      alert('ÊöÇÊó†Êï∞ÊçÆ')
      return
    }

    const totalItems = data.reduce((sum, s) => sum + s.count, 0)
    const firstRecord = data[data.length - 1]?.formattedTime || 'Êó†'
    const lastRecord = data[0]?.formattedTime || 'Êó†'

    const message = `
üìä ÁªüËÆ°‰ø°ÊÅØ:
- ÊÄªÂø´ÁÖßÊï∞: ${data.length}
- ÊÄªÊù°ÁõÆÊï∞: ${totalItems}
- È¶ñÊ¨°ËÆ∞ÂΩï: ${firstRecord}
- ÊúÄÂêéËÆ∞ÂΩï: ${lastRecord}
    `.trim()

    alert(message)
  } catch (e) {
    showError(`Ëé∑ÂèñÁªüËÆ°Â§±Ë¥•: ${e instanceof Error ? e.message : 'Êú™Áü•ÈîôËØØ'}`)
  }
}

// Ëß¶ÂèëÊâãÂä®ÊäìÂèñÔºàÈÄöËøá GitHub ActionsÔºâ
const triggerManualFetch = async () => {
  if (!isConfigured.value) {
    showError('ËØ∑ÂÖàÈÖçÁΩÆ GitHub ‰ø°ÊÅØ')
    return
  }

  isFetching.value = true
  error.value = ''
  showStatus('üîÑ Ê≠£Âú®Ëß¶Âèë GitHub Actions...', 'info', 0)

  try {
    // ÊñπÊ≥ï 1: ÈÄöËøá GitHub API Ëß¶Âèë WorkflowÔºàÈúÄË¶Å TokenÔºâ
    // ÊñπÊ≥ï 2: ÊèêÁ§∫Áî®Êà∑ÊâãÂä®Ëß¶Âèë
    const message = `
‚ö†Ô∏è ÊâãÂä®Ëß¶ÂèëÊäìÂèñ

Áî±‰∫é GitHub ÂÆâÂÖ®ÈôêÂà∂ÔºåÂâçÁ´ØÊó†Ê≥ïÁõ¥Êé•Ëß¶Âèë Actions„ÄÇ

ËØ∑Êåâ‰ª•‰∏ãÊ≠•È™§Êìç‰ΩúÔºö

1. ËÆøÈóÆ‰Ω†ÁöÑ GitHub ‰ªìÂ∫ì
2. ËøõÂÖ• Actions Ê†áÁ≠æÈ°µ
3. ÊâæÂà∞"Ëá™Âä®ÊäìÂèñÁü•‰πéÁÉ≠Ê¶ú"
4. ÁÇπÂáª"Run workflow"
5. Á≠âÂæÖ 10-20 Áßí
6. ËøîÂõûÈ°µÈù¢ÁÇπÂáª"‰ªé GitHub Âä†ËΩΩ"

ÊàñËÄÖÁ≠âÂæÖ‰∏ã‰∏ÄÊ¨°Ëá™Âä®ÊäìÂèñÔºàÊØè30ÂàÜÈíüÔºâ

ÊèêÁ§∫Ôºö‰Ω†‰πüÂèØ‰ª•Ê∑ªÂä† GitHub Token Êù•Ëá™Âä®Ëß¶Âèë
    `.trim()

    alert(message)
    showStatus('‚ÑπÔ∏è ËØ∑ÊâãÂä®Ëß¶Âèë GitHub Actions', 'info')
  } catch (e) {
    showError(`Ëß¶ÂèëÂ§±Ë¥•: ${e instanceof Error ? e.message : 'Êú™Áü•ÈîôËØØ'}`)
  } finally {
    isFetching.value = false
  }
}

// Âø´ÁÖßÂ±ïÂºÄ/Êî∂Ëµ∑
const toggleSnapshot = (id: string) => {
  if (expandedSnapshots.value.has(id)) {
    expandedSnapshots.value.delete(id)
  } else {
    expandedSnapshots.value.add(id)
  }
}

// Ê∏ÖÁ©∫ÂéÜÂè≤
const clearHistory = () => {
  if (confirm('Á°ÆÂÆöË¶ÅÊ∏ÖÁ©∫ÊâÄÊúâÂéÜÂè≤ËÆ∞ÂΩïÂêóÔºüÔºà‰ªÖÊú¨Âú∞ÁºìÂ≠òÔºå‰∏çÂΩ±Âìç GitHub ‰∏äÁöÑÊï∞ÊçÆÔºâ')) {
    snapshots.value = []
    expandedSnapshots.value.clear()
    showStatus('‚úÖ ÂéÜÂè≤ËÆ∞ÂΩïÂ∑≤Ê∏ÖÁ©∫', 'info')
  }
}

// Ê∏ÖÈô§ÁºìÂ≠ò
const clearCache = () => {
  localStorage.removeItem('zhihu_cache_data')
  localStorage.removeItem('zhihu_cache_timestamp')
  showStatus('‚úÖ ÁºìÂ≠òÂ∑≤Ê∏ÖÈô§', 'success')
}

// ÁîüÂëΩÂë®Êúü
onMounted(() => {
  loadConfig()
})
</script>

<style scoped>
